import config from "../config/index";
import {
    makeAxiosGetRequest, makeAxiosMultiPartRequest, makeAxiosPostRequest
} from "./http-service";


/**
 * createPost
 * @param {object} data
 */
export const getCategories = (data) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(config.baseUrl + "gig-categories", true, data)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                reject(e.response);
            });
    });
};

