import React from 'react';
import { connect } from 'react-redux';
import { store } from '../store';
import {SWITCH_LOADER,ALERTBOX, MODAL} from '../constants/actionTypes';



const _switchLoader = (isActive=false,loaderText) => {
    store.dispatch({ type: SWITCH_LOADER, isActive: isActive, loaderText: loaderText })
}
const _triggerAlert = (alertOpen=false,message='',type='error') => {
    store.dispatch(
            {type:ALERTBOX,
            alertOpen: alertOpen,
            alertMessage: message,
            alertType: type}
        )    
}

const _modal = (modalOpen=false, modalTitle="", content="", yesBtnText="Yes", type='general', props={}) => {
console.log(modalTitle);
        store.dispatch({
            type: MODAL,
            modalOpen: modalOpen,
            modalContent: content,
            modalType: type,
            modalProps: props,
            modalTitle: modalTitle,
            modalYesBtnText: yesBtnText
          })
}
export const switchLoader = _switchLoader;
export const alertBox = _triggerAlert;
export const modal = _modal;
