export const updateProfile = (data) => {
    return new Promise((resolve, reject) => {
        makeAxiosMultiPartRequest(config.baseUrl + "updateProfile", true, data)
        .then(res => {
            console.log(res.data.token);
            localStorage.setItem('jwt', res.data.token);
            localStorage.setItem('currentUser', JSON.stringify(res.data.userinfo));
            agent.setToken(res.data.token);
            resolve(res.data);
        })
        .catch(e => {
            reject(e.response);
        });
    });
};

export const walletAuth = (data) => {
    return new Promise((resolve, reject) => {
        makeAxiosPostRequest(config.baseUrl + "wallet-auth", true, data)
            .then(res => {
                try {
                    console.log(res.data.token);
                    localStorage.setItem('walletToken', res.data.token);
                    agent.setWalletToken(res.data.token);
                } catch (error) {
                    console.log(error);
                }
                resolve(res.data);
            })
            .catch(e => {
                
                reject(e.response);
            });
    });
};

