import React, {useEffect, useState} from 'react';
import Spinner from '../Spinner/index';
import A from '../A';
import Truncate from 'react-truncate';
import {history} from '../../store';
import './styles2.css';
import { Link } from 'react-router-dom';
import { pic, profilePic } from '../../globalFunctions';
import Button from '../../components/Button';

  
const TopThumbCard = props => {
  const [post, setPost] = useState(props.post);
  const [request, setRequest] = useState(props.request);
  const [reported, setReported] = useState(props.reported);
  const goTo = (url) => {
    history.push(url);
  };
  useEffect(() => {
    setPost(props.post);
    setReported(props.reported);
  }, [props.post, props.reported]); 
  return (
    <div className="card topThumbCard">
    <A href={props.url} className="block"><div className="thumb" style={{backgroundImage: `url(${pic(post.contents[0] != undefined ? post.contents[0].content_url : '')})`}} >
    {post.status == 'draft' || post.estatus == 'draft' && <small className="badge badge-secondary draft-label ml-2 mt-1">Draft</small> } 
      </div></A>
    <div className="body">
      <div className="row-b">
    <A href={"/u/"+post.userid} className="userinfo">
      <div className="pic" style={{backgroundImage: `url(${ profilePic(post.userinfo.avatar)})`}}></div>
      <div className="">
        <div className="name ">{post.userinfo.name}</div>
       
      </div>
    </A>
    {
        props.currentUser != undefined && post.shared == undefined && post.userid == props.currentUser._id ? <span className="list-group-item  p-1 pr-2 pointer  dropdown pull-right">
        
        <i className="dots fa fa-ellipsis-h"></i>
        <div className="dropdown-menu hasUpArrow dropdown-menu-right">
            <Link to={props.base+post.slug} className="dropdown-item">
            <img className="mr-1" src={require("../../assets/images/edit-icon.png")} />
            <span className="m-1">Edit</span>
            </Link> 
        
        {props.currentUser != undefined && post.sharedBy == props.currentUser._id || post.userid == props.currentUser._id && <a className="dropdown-item"  onClick={(e) => props.removePost(post._id)}>
            <img className="mr-1" src={require("../../assets/images/remove-icon.png")} />
            <span className="m-1">Remove</span></a>}
    </div>
    </span>
    :
    <span className="list-group-item  p-1 pr-2 pointer  dropdown pull-right ">
        
        <i className="dots fa fa-ellipsis-h"></i>
        {
          reported ?
          <div className="dropdown-menu hasUpArrow dropdown-menu-right">
            <a onClick={()=>props.unReport()} className="dropdown-item">
            <i className="fa fa-undo"></i>
                <span className="m-1">Undo Report</span></a> 
        </div>
          :
          <div className="dropdown-menu hasUpArrow dropdown-menu-right">
            <a onClick={()=>props.report()} className="dropdown-item">
            <i className="fa fa-exclamation-circle"></i>
                <span className="m-1">Report</span></a> 
        </div>
        }
        
    </span>
      }
      </div>
    
    <A href={props.url}><div className="card-title">
      <Truncate lines={2}>{post.subject}</Truncate>
      <div className="rating">{ !request && <React.Fragment><i className="fa fa-star"></i> {post.rating} ({post.ratingCount}) </React.Fragment> }</div>
    </div></A>
    {
            post.waiting > 0 && <A href={"/passionomy/dashboard/gig/"+post._id}><span className="waiting">
          {post.waiting+" Orders waiting" }
          </span></A>
        }
    <div className="highlight-meta">

      {request ? "Budget" : "Starting from"} <div className="price">{post.preferedCurrency +" " +(post.standardPrice == undefined ? post.budget : post.standardPrice)}</div>
      {/* <A href={"/passionomy/gig/"+post._id}><Button  className="w-100 mt-1">View</Button></A> */}
    </div>
    </div>
  </div>
  );

};

export default TopThumbCard;