import React, {useEffect, useState} from 'react';
import Spinner from '../Spinner/index';
import A from '../A';
import { GetAssetImage } from '../../globalFunctions';
import './styles.css';

  
const Button = props => {
    const [layout, setLayout] = useState(props.layout != undefined ? props.layout : "default");
    // const [size, setSize] = useState(props.size != undefined ? props.size : "");
    
  return (
    <React.Fragment>
    {
      layout == 'dropdown' ?
      <button disabled={props.disabled == null ? false : props.disabled} type={props.type == null ? "submit" : props.type} className={"button "+props.variant+" "+props.size+" "+props.className+" dropdown"}>
      <span className="btn-text">{props.children}</span>
      <i className="fa fa-chevron-down"></i>
      {
        <div className="dropdown-items">
        {
        props.dropdownOptions.map((e, i)=>{
          return <div className="item" onClick={() => props.onClick(i)}>{e}</div>
        })
        }
        </div>
      }
      </button>
      :
      <button disabled={props.disabled == null ? false : props.disabled}  onClick={props.onClick} type={props.type == null ? "submit" : props.type} className={"button "+props.variant+" "+props.size+" "+props.className}>
      {props.children}
      </button>
    }
    </React.Fragment>
   
  );

};

export default Button;