import React from 'react';
import Button from '../../components/Button';
import {postReport, postUnReport} from '../../http/http-calls';
import {removeGig, removeGigRequest} from '../../http/gig-calls';
import { switchLoader, alertBox, modal } from '../../commonRedux/';
import TopThumbCard from '../../components/Cards/TopThumbCard';
import Modal from '../../components/Popup';

class GigRequest extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            removed: false,
            reported: props.post.reported,
            gig: props.post,
            category: '',
            reason: '',
            reportModal:false,
            lastData: null,
            lastI: null,
            lastType:null
        }
    }
    reportModal = () => {
        this.setState({reportModal: !this.state.reportModal}) // true/false toggle
    };

    report = () => {
            var link = "gigonomy/gig-request/"+this.state.gig._id;
            postReport({ id: this.state.gig._id, type: 'gigrequest', link, category: this.state.category, reason: this.state.reason })
            .then(async resp => {
                alertBox(true, resp.message, 'success');
                this.setState({ reportModal: false, reported: true });
            }, error => {
                alertBox(true, error.data.message);
                this.setState({ reportModal: false});
            });
    
    };

    unReport = () => {
        postUnReport({ id: this.state.gig._id })
        .then(async resp => {
            alertBox(true, resp.message, 'success');
            this.setState({ reportModal: false, reported: false });
        }, error => {
            alertBox(true, error.data.message);
        });
    };

    removePost = () => {
        removeGigRequest({ id: this.state.gig._id })
        .then(async resp => {
            alertBox(true, resp.message, 'success');
            this.setState({  removed: true });
        }, error => {
            alertBox(true, error.data.message);
        });
    };

    componentDidMount = () => {
        
    }

    

    render() {
        const { gig } = this.state;
        return (
            <React.Fragment>
            {
                this.state.removed ?
                null
                :
                <div className="" key={this.state.gig._id}>
                <Modal displayModal={this.state.reportModal} closeModal={this.reportModal} >
                     <div>
                         <div className="form-group">
                             <select className="form-control" value={this.state.category}  onChange={(e) => this.setState({category: e.target.value})}>
                                 <option value="">Select Category</option>
                                 <option>Violence</option>
                                 <option>Racism / Hatespeech</option>
                                 <option>Pornographic</option>
                                 <option>Spam</option>
                                 <option>Other</option>
                             </select>
                         </div>
                         <div className="form-group">
                             <textarea type="text" placeholder="Reason" className="form-control" onChange={(e) => this.setState({reason: e.target.value})} value={this.state.reason} />
                         </div>
                     </div>
                <div className="">
                <Button variant="primary" size="compact m-2" onClick={() => this.report()}>Report</Button>
                <Button variant="secondary" size="compact m-2" onClick={this.reportModal}>Cancel</Button>
                </div>
             </Modal>
             <TopThumbCard reported={this.state.reported}  report={() => this.reportModal()} unReport={()=>this.unReport()} url={"/passionomy/request/"+gig.slug} currentUser={this.props.currentUser} post={gig} removePost={this.removePost} base={"/passionomy/request/edit/"} request={true} />
            </div>
            }
            </React.Fragment>
        );
    }
}

export default GigRequest;