import config from "../config/index";
import {
    makeAxiosGetRequest,
    makeAxiosPostRequest
} from "./http-service";

export const getTrendingHashtags = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(config.baseUrl + "get-trending-hashtags", false, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                
                reject(e.response);
            });
    });
};

