import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import {getToken, getCurrentUser} from '../http/token-interceptor';
const PrivateRoute = ({ component: Component, currentUser, openChat, openedChats, ...rest }) => (
    <Route {...rest} render={props => (getToken() != null ? <Component {...props} currentUser={currentUser} openChat={openChat} openedChats={openedChats} /> :  
    <Redirect to="/login" />)} />                         
)


export default PrivateRoute;