import config from "../config/index";
import {
    makeAxiosGetRequest,
    makeAxiosPostRequest
} from "./http-service";

export const getChats = (params) => {
    return new Promise((resolve, reject) => {

        makeAxiosGetRequest(`${config.baseUrl}${"get-chats"}`, true, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                
                reject(e.response);
            });
    });
};

