import React, { useState, useEffect } from 'react';
import A from '../A';
import {getCategories } from '../../http/gig-calls';
import Button from '../Button';
import Slider from '@material-ui/core/Slider';
import {GetAssetImage} from '../../globalFunctions';
import './styles.scss';

const GigFilter = props => {

  const [category, setCategory] = useState('');
  const [subCategory, setSubCategory] = useState('');
  const [priceFrom, setPriceFrom] = useState('');
  const [priceTo, setPriceTo] = useState('');
  const [rating, setRating] = useState(0);
  const [categories, setCategories] = useState([]);
  const [subCategories, setSubCategories] = useState([]);
  const [key, setKey] = useState('');
  useEffect(() => {
    getData();
  }, []);

  const getData = (l = 1, p = '') => {
    getCategories({level: l, parent: p}).then(resp => {
      if(l == 1){
        setCategories(resp);
        setCategory("");
      }else{
        setSubCategory("");
        setSubCategories(resp)
      }
    }, err => {
      console.log(err);
    })
  }

  useEffect(() => {
    var data = {
      category,
      subCategory,
      priceFrom,
      priceTo,
      rating,
      key
    };
    console.log(data);
    props.dataChange(data);
  },[rating, category, subCategory, priceFrom, priceTo, key]);

    return (
        <div className="widget empty-inner-container-with-border filterWidget">
          
        <div className="container">
          <div className="row">
          <ul className="list-group w-100"><li className="widgetTitle">
          <img src={GetAssetImage("Icon feather-filter.png")} />
            <span>Filter</span>
            </li></ul>
          <form className="form p-3 w-100">
          <div className="form-group">
            <label>Search Keyword</label>
            <input type="text" className="form-control" placeholder="eg: Logo Design" value={key}  onChange={(e) => {setKey(e.target.value)}}  />
          </div>
          
          <div className="form-group">
            <label>Category</label>
            <select className="form-control" value={category} onChange={(e) => {setCategory(e.target.value); getData(2, e.target.value)}} ><option value="">All</option>
            {
              categories.length > 0 && categories.map((e, i) => {
                return <option value={e._id}>{e.category}</option>
              })
            }
            </select>
          </div>
          
          <div className="form-group">
            
            <label>Sub Category</label>
            <select className="form-control" value={subCategory} onChange={(e) =>{ setSubCategory(e.target.value)}} ><option value="">All</option>
            {
              subCategories.length > 0 && subCategories.map((e, i) => {
                return <option value={e._id}>{e.category}</option>
              })
            }
            </select>
          </div>
          <div className="form-group">
            <label>Price Range</label>
            <div className="d-flex align-items-center justify-content-between">
            <input type="text" className="form-control" placeholder="$" value={priceFrom}  onChange={(e) => {setPriceFrom(e.target.value)}} />
            <div className="px-2">-</div>
            <input type="text" className="form-control" placeholder="$" value={priceTo} onChange={(e) => {setPriceTo(e.target.value)}}  />
            </div>
          </div>
          <div className="form-group">
            <label>Rating</label>
            <div className="d-flex align-items-center justify-content-start starRating">
            
            <i className={"fa pr-1 "+(rating >= 1 ? 'fa-star' : 'fa-star-o')} onClick={() => {setRating(1)}}></i>
            <i className={"fa pr-1 "+(rating >= 2 ? 'fa-star' : 'fa-star-o')} onClick={() => {setRating(2)}}></i>
            <i className={"fa pr-1 "+(rating >= 3 ? 'fa-star' : 'fa-star-o')} onClick={() => {setRating(3)}}></i>
            <i className={"fa pr-1 "+(rating >= 4 ? 'fa-star' : 'fa-star-o')} onClick={() => {setRating(4)}}></i>
            <i className={"fa "+(rating >= 5 ? 'fa-star' : 'fa-star-o')} onClick={() => {setRating(5)}}></i>
           {/*  <div className="px-2 text-small text-secondary">{rating} and up</div>
            {rating > 0 && <i className={"fa fa-times pointer"} onClick={() => {setRating(0)}}></i>} */}
            </div>
          </div>

          {/* <div className="form-group">
          <div className="form-check">
            <input   type="checkbox" value="" id="flexCheckCheckedFriendGig" checked/>
            <label   for="flexCheckCheckedFriendGig">
           Show only my friend's gigs
            </label>
            
          </div>
          </div>

          <div className="form-group">
          <div className="form-check">
            <input  type="checkbox" value="" id="flexCheckCheckedPopular" />
            <label  for="flexCheckCheckedPopular">
            Show popular right now
            </label>
            
          </div>
          </div> */}
         
          </form>

            </div>
            </div>
        </div>
    )
}

export default GigFilter;