import React, {useEffect, useState} from 'react';
import Spinner from '../Spinner/index';
import './styles.css';

  
const TabsUI = props => {
    const [currentTab, setCurrent] = useState(props.currentTab);
    const [tabs, setTabs] = useState(props.tabs);
    const [panels, setPanels] = useState(props.panels);
    const [content, setContent] = useState(props.content);

    const handleChange = (newValue) => {
        if(props.preventChange == true){
            var con = window.confirm("Are you sure want to leave?");
            console.log(con);
            if(con == true){
                setCurrent(newValue);
                props.changeTab(newValue);
            }
        }else{
       setCurrent(newValue);
       props.changeTab(newValue);
        }
    };
    useEffect(() => {
        if(content != null){
            setContent(content);
        }
    }, [props.currentTab]);



  return (
<div className="tabs">
    <div className={"tabBar "+(props.width != null && " "+props.width)}>
        {
            tabs != null && tabs.length > 0 && tabs.map((e, i) => {
                return <div key={i} className={"tabBarItem"+(currentTab == i ? " active" : "")} onClick={(e) => handleChange(i)}>
                {e}
            </div>
            })
        }
    </div>
    <div className="tabPanel">
        {
            panels != null && tabs.length > 0 && tabs.map((e, i) => {
                return <div key={i} className={"tabPanelItem "+(currentTab == i ? " active" : "")}>
                {e}
            </div>
            })
        }
        {
            content != undefined && content != null ? content : null
        }
    </div>
</div>
  );

};

export default TabsUI;