import React, { useState, useEffect } from 'react';
import { alertBox } from '../../commonRedux';
import config from '../../config';
import walletCheck from '../../hooks/walletCheck';
import A from '../A';
import './styles.scss';

const WalletMenu = props => {
    const p2p = () =>{
       walletCheck().then(resp=>{
            try {
                    if(resp.data.status == 'Accept'){
                      window.open(config.p2pBase+"?token="+localStorage.getItem("walletToken"));
                    }
            } catch (error) {
              alertBox(true, "You need to finish KYC to use P2P");
              props.history.push("/wallet/verification");
            }
           
        }, err => {
          alertBox(true, "You need to finish KYC to use P2P");
          props.history.push("/wallet/verification");
        })
      
    }
    return (
        <div className="widget empty-inner-container-with-border SocialMenus">
        <div className="container">
          <div className="row">

            <ul className="list-group w-100">
              <li className="list-group-item d-flex justify-content-between align-items-center header-drak widgetTitle">
                Menu
              </li>
            </ul>

            <ul className="list-group w-100 menu">
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/deposit' && 'current')}>
                <A href="/wallet/deposit" className="align-items-center d-flex"><span className="aicon fa fa-bank"></span> Deposit</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "}>
                <span onClick={p2p}  className="align-items-center d-flex pointer"><span className="aicon fa fa-retweet"></span> P2P</span>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/transfer' && 'current')}>
                <A href="/wallet/transfer" className="align-items-center d-flex"><span className="aicon fa fa-angle-double-right"></span> Send / Transfer</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/transactions' && 'current')}>
                <A href="/wallet/transactions" className="align-items-center d-flex"><span className="aicon fa fa-exchange "></span> Transactions</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/verification' && 'current')}>
                <A href="/wallet/verification" className="align-items-center d-flex"><span className="aicon fa fa-check"></span> Verification</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/tfa' && 'current')}>
                <A href="/wallet/tfa" className="align-items-center d-flex"><span className="aicon fa fa-shield"></span> TFA</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path.indexOf('/wallet/address/') != -1 && 'current')}>
                <A href="/wallet/address/BTC" className="align-items-center d-flex"><span className="aicon fa fa-book"></span> Address Book</A>
              </li>
              
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/trading' && 'current')}>
                <A href="/wallet/trading" className="align-items-center d-flex"><span className="aicon fa fa-line-chart"></span> Trade</A>
              </li>
              <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/withdraw' && 'current')}>
                <A href="/wallet/withdraw" className="align-items-center d-flex"><span className="aicon fa fa-angle-double-down"></span> Withdraw</A>
              </li>
              {/* <li className={"list-group-item d-flex justify-content-between align-items-center "+(props.match.path == '/wallet/api' && 'current')}>
                <A href="/wallet/api" className="align-items-center d-flex"><span className="aicon fa fa-key"></span> API Keys</A>
              </li> */}
            </ul>
            </div>
            </div>
        </div>
    )
}

export default WalletMenu;