class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        minimized: [],
        chats: []
    };
    this.minimize = this.minimize.bind(this);
    this.close = this.close.bind(this);
  }

  minimize = (id) => {
    var temp = this.state.chats;
    //var foundIndex = temp.findIndex(x => x.id == id);
    var foundIndex = id;
    var temp2 = this.state.minimized;
    temp2.push(temp[foundIndex]);
    // temp.splice(foundIndex, 1);
    temp[foundIndex].minimized = true;
    
    this.setState({
      chats: temp,
      minimized: temp2
    });
  }

  close = (id) => {
    var temp = this.state.chats;
    var foundIndex = id;
    temp.splice(foundIndex, 1);
    
    this.setState({
      chats: temp
    });
  }

  restore = (id) => {
    var temp = this.state.minimized;
    var foundIndex = temp.findIndex(x => x._id == id);
    temp.splice(foundIndex, 1);

    var temp2 = this.state.chats;
    var foundIndex = temp2.findIndex(x => x._id == id);
    temp2[foundIndex].minimized = false;
    
    this.setState({
      chats: temp2,
      minimized: temp
    });
  }



  componentDidMount = () => {
    

    this.setState({
      chats: this.props.chats
    })
  };

  // componentDidUpdate = (prevProps) => {
  //   if(this.props.location.pathname != prevProps.location.pathname){
  //     
  //   if(this.props.location.pathname.indexOf("messenger") !== -1){
  //     this.setState({
  //       chats: [],
  //       minimized: []
  //     })
  //   }
  // }
  // }

  render() {
    return (
      <div className="footer">
        {
          this.state.chats.length > 0 ? this.state.chats.map((r, i) => {
            return (r.minimized ? null : <PopChat minimize={this.minimize} close={this.close} id={i} key={i} chat={r} {...this.props} />)
          }) : null
        }
        <div className="minimizedChats">
          {
            this.state.minimized.length > 0 ? this.state.minimized.map((r, i) => {
              return <img src={profilePic(r.user1._id == this.props.currentUser._id ? r.user2.avatar: r.user1.avatar)} className="minimizedChatPic" onClick={(e) => this.restore(r._id)}/>
            }) : null
          }
        </div>
      </div>
    );
  }
}

export default withRouter(Footer);
