import React from "react";
import { Link } from "react-router-dom";
import { alertBox } from "../../commonRedux/";
import A from "../../components/A";
import Button from "../../components/Button";
import GigFilter from "../../components/Filter/gigFilter";
import CreateGigWidget from "../../components/Gigs/Create";
import Gig from "../../components/Gigs/Gig";
import GigRequest from "../../components/Gigs/GigRequest";
import PopularGigs from "../../components/Gigs/PopularGigs";
import Modal from "../../components/Popup";
import TabsUI from "../../components/Tabs/index";
import { allGigRequests, getGigs } from "../../http/gig-calls";
import { postReport, postUnReport } from "../../http/http-calls";
import Images from "../../assets/images/images";
import AdminActivity from "./adminActivity/index.js";
import "./gigs.css";
import SavedGroups from "../../components/SavedGroups/index";
import GroupMembers from "../../components/GroupMembers/index";
import GroupWidget from "../../components/GroupWidget";
import SocialActivities from "../../components/Menu/SocialActivities";
import Header from "../../components/Header";
import images from "../../assets/images/images";

class ViewEditGroupAdmin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      limit: 4,
      page: 1,
      cid: 0,
      posts: [],
      category: "",
      reason: "",
      reportModal: false,
      data: {
        category: "",
        subCategory: "",
        priceFrom: "",
        priceTo: "",
        rating: "",
        key: "",
      },
      lastData: null,
      lastI: null,
      lastType: null,
      gigs: [],
      currentTab: 0,
    };
  }

  reportModal = (p = null, i = null, t = null) => {
    this.setState({
      lastData: p,
      lastI: i,
      lastType: t,
    });
    this.setState({ reportModal: !this.state.reportModal }); // true/false toggle
  };

  report = () => {
    var lastData = this.state.lastData;
    var lastI = this.state.lastI;
    var link = "gigonomy/gig/" + lastData._id;
    postReport({
      id: lastData._id,
      type: this.state.lastType,
      link,
      category: this.state.category,
      reason: this.state.reason,
    }).then(
      async (resp) => {
        alertBox(true, resp.message, "success");
        var temp = this.state.posts;
        temp[lastI].reported = 1;
        this.setState({ reportModal: false, posts: temp });
      },
      (error) => {
        alertBox(true, error.data.message);
        this.setState({ reportModal: false });
      }
    );
  };
  unReport = (post, i) => {
    postUnReport({ id: post._id }).then(
      async (resp) => {
        alertBox(true, resp.message, "success");
        var temp = this.state.posts;
        temp[i].reported = 0;
        this.setState({ reportModal: false, posts: temp });
      },
      (error) => {
        alertBox(true, error.data.message);
      }
    );
  };

  componentDidMount = () => {
    // this.getData();
    // this.getRequests();
  };

  categoryChange = (c) => {
    this.setState({
      cid: c,
      currentTab: 0,
    });
  };

  dataChange = (data = {}) => {
    //this.props.history.push("/passionomy/gig");
    this.getData(data);
    this.getRequests(data);
  };

  getData = (data = {}) => {
    this.setState({
      gigs: [],
    });
    var d = data;
    console.log(d);
    d["page"] = this.state.page;
    d["limit"] = this.state.limit;
    d["userid"] = this.props.currentUser._id;
    getGigs(d).then(
      (resp) => {
        this.setState(
          {
            gigs: resp.post,
          },
          () => {
            console.log(this.state.gigs);
          }
        );
      },
      (err) => {
        console.log(err);
      }
    );
  };

  getRequests = (data = {}) => {
    this.setState({
      posts: [],
    });
    var d = data;
    d["page"] = this.state.page;
    d["limit"] = this.state.limit;
    d["userid"] = this.props.currentUser._id;
    allGigRequests(data).then(
      (resp) => {
        this.setState({
          posts: resp,
        });
      },
      (err) => {}
    );
  };
  changeTab = (newValue) => {
    this.setState({ currentTab: newValue });
  };

  render() {
    return (
      <div className="viewEditGroupModerator">
        <Header
          appName={this.props.appName}
          currentUser={this.props.currentUser}
        />
        <div className="container my-wall-container ">
          <div className="row mt-2">
            {/* <!-- left column --> */}
            <div className="col-sm empty-container-with-out-border left-column">
              <SocialActivities />
              <div className="groupAreaWrapper">
                <div className="group">
                  <span className="groupName">
                    Group Name of user will be shown here
                  </span>
                  <div className="groupAccessType">
                    <img src={images.locked} alt="locked" />
                    <span>Private Group</span>
                  </div>
                  <span className="groupDescription">
                    Developing a solid brand makes selling easier. Brand
                    Strategy and Design communicates your ease so that you can
                    rise above the competition.
                  </span>
                  <div className="float-right">
                    <Link className="float-right">
                      See More <i className="fa fa-caret-down"></i>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            {/* <!-- end left column --> */}

            {/* <!-- center column --> */}
            <div className="col-sm empty-container-with-out-border center-column">
              <div className="groupSettingsArea">
                <AdminActivity />
              </div>
            </div>
            {/* <!-- end center column --> */}

            {/* <!--  right column --> */}
            {/* <!--  right column --> */}
            <div className="col-sm empty-container-with-out-border right-column">
              <SavedGroups />
              <GroupMembers />
              <GroupWidget />
            </div>
            {/* <!-- end right column --> */}
            {/* <!-- end right column --> */}
          </div>
        </div>
      </div>
    );
  }
}

export default ViewEditGroupAdmin;
