import React, { useState, useEffect } from 'react';
import A from '../A';
import { store, history } from '../../store';
import './styles.scss';
import config from '../../config';

const WalletMenu = props => {
    const changeMenu = (e) => {
      var val = e.target.value;

          if(val.indexOf("http") != -1){
                window.open(val);
            }else{
                history.push(val);
            }
    }
    return (
        <div className=" empty-inner-container-with-border SocialMenus mobileMenu">
        <div className="">
          <div className="row">

            <ul className="list-group w-100 mb-0">
              <li className="list-group-item d-flex justify-content-between align-items-center header-drak widgetTitle text-white">
                Wallet Menu
              </li>
            </ul>
            <select className="list-group w-100 menu  dropdownBoxMenu" onChange={(e) => changeMenu(e)}>
              <option value="/wallet/deposit">
                  Deposit
              </option>
              <option value={config.p2pBase+"?token="+localStorage.getItem("walletToken") }>
                   P2P
               </option>
               <option value="/wallet/transfer">
                   Send / Transfer
               </option>
               <option value="/wallet/transactions">
                   Transactions
               </option>
               <option value="/wallet/verification">
                   Verification
               </option>
               <option value="/wallet/tfa">
                   TFA
               </option>
               <option value="/wallet/address/BTC">
                   Address Book
               </option>
               <option value="/wallet/trading">
                   Trading
               </option>
               <option value="/wallet/withdraw">
                   Withdraw
               </option>
               {/* <option value="/wallet/api">
                   API Keys
               </option> */}
            </select>
            </div>
            </div>
        </div>
    )
}

export default WalletMenu;