import React from 'react';
import { Link } from 'react-router-dom';
import { createPost, getCSRF, getTagUsers, getUsersForTag } from '../../http/http-calls';
import { twitterTweet} from '../../http/twitter-calls';
import { image, video, file } from '../../config/constants';
import ReactTags from 'react-tag-autocomplete';
import {switchLoader,alertBox} from '../../commonRedux/';
import ContentEditable from 'react-contenteditable';
import Loader from 'react-spinners/BarLoader';


class CreatePost extends React.Component {

	constructor(props) {
		super(props);
		this.postFileInput = React.createRef();
		this.reactTags = React.createRef()
		this.contentEditable = React.createRef();
		this.state = {html: "<b>Hello <i>World</i></b>"};

		this.state = {
			twitterID: 0,
			twitter: false,
			text: '',
			loadTxt: '',
			url: '',
			profileImg: '',
			postImgs: [],
			postAccept: '',
			visibility: 'public',
			tags: [],
			toggleTagInput: false,
			suggestions: [],
			displaySelectedItem:[]
		}
		
		this.handleChangeImage = this.handleChangeImage.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.extractUrl = this.extractUrl.bind(this);
		this.onDelete = this.onDelete.bind(this);
		this.removeURL = this.removeURL.bind(this);
	
	}

	componentDidMount() {

		// getCSRF()
		// 	.then(async resp => {
		// 		localStorage.setItem('csrf', resp._csrf);
		// 	}, error => {
		// 		
		// 	});
		console.log(this.props.currentUser);
		this.setState({twitterID: this.props.currentUser.twitterID});
	}

	enableUpload = (acceptExt) => {
		let accept = acceptExt.join(',');
		this.setState({ postAccept: accept }, () => {
			this.postFileInput.current.click();
		});
	}

	changeTwitter = () => {
		this.setState({
			twitter: !this.state.twitter
		})
	}
	changeVisibility = () => {
		this.setState({
			visibility: this.state.visibility === 'public' ? 'onlyme' : 'public'
		})
	}
	removeURL = () => {
		this.setState({
			url: ''
		})
	}

	handleKeyPress = (e) => {
		console.log(e.key);
        if (e.key === 'Enter') {
            this.setState({
				text: this.state.text + "\n"
            });
        }
	}
	
	clearCreatPostInputs = () => {
		this.setState({
			text: '',
			url: '',
			profileImg: '',
			tags: [],
			postImgs: [],
			postAccept: '',
			displaySelectedItem:[],
			visibility: 'public',
		})
	}
	switchLoader2 = (txt = '') => {
		this.setState({
			loadTxt : txt
		})
	}
	removeSelected = () => {
		this.setState({
			displaySelectedItem: [],
			postImgs: []
		})
	}
	post = () => {
		const formData = new FormData()
		var text = this.state.text;
		console.log(text);
		text = text.replace("&nbsp;", "");
		formData.append('postImgs', this.state.postImgs);
		formData.append('text', text);
		formData.append('subject', 'Eclipton');
		formData.append('taggPeople', JSON.stringify(this.state.tags));
		formData.append('privacy', this.state.visibility);
		formData.append('twitterPost', this.state.twitter);
		formData.append('url', this.state.url);
		 this.switchLoader2('Your post is being created...')
		createPost(formData)
			.then(async resp => {
				if(resp.success){
					this.props.setState('latestpost',resp.post);
					if(this.state.twitter){
						twitterTweet({text: this.state.text}).then(async resp => {
							this.clearCreatPostInputs();
							this.setState({
								tweet: false
							});
						});
					}else{
						this.clearCreatPostInputs();
					}
				}
				this.switchLoader2();
			}, error => {
				
				this.switchLoader2()
			});
	}

	handleChangeImage(e) {
		this.setState({ postImgs: e.target.files[0] },() => {
			let getExt = (this.state.postImgs.name.split('.')).pop();
			var displaySelectedItem = [];
			if(image.indexOf('.'+getExt) !== -1){
				let imgTag = () => (<img className="displayPostImg" src={URL.createObjectURL(this.state.postImgs)} />);
				displaySelectedItem.push(imgTag());
			}else if(video.indexOf('.'+getExt) !== -1){
				displaySelectedItem.push("1 Video Selected");
			}else if(file.indexOf('.'+getExt) !== -1){
				displaySelectedItem.push("1 File Selected");
			}
			this.setState({displaySelectedItem:displaySelectedItem});
		})
	}

	// handleChange = (evt) => {
	// 	const { name, value } = evt.target;
	// 	this.setState({ [name]: value }, () => {
	// 	});
	// }
	extractUrl = (text) => {
		var expression = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/gi;
		var regex = new RegExp(expression);

		return text.match(regex)
	}
	htmlToText = (html) => {
		var temp = document.createElement('div');
		temp.innerHTML = html;
		return temp.textContent; // Or return temp.innerText if you need to return only visible text. It's slower.
	};

	handleChange = evt => {
		//var txt  = this.htmlToText(evt.target.value);
		var txt  = evt.target.value;
		this.setState({text: txt}, () => {
			var temp = this.extractUrl(this.state.text);
			if(temp != null && temp.length > 0 && this.url != temp[0]){
				this.setState({
					url: temp[0]
				});
			}
		});
	};
	onDelete(i) {
		const tags = this.state.tags.slice(0)
		tags.splice(i, 1)
		this.setState({ tags })
	}
	onAddition(tag) {
		const tags = [].concat(this.state.tags, tag)
		this.setState({ tags })
	}

	toggleTag = () => {
		if(!this.state.toggleTagInput){
			getUsersForTag({key: ''}, true)
			.then(async resp => {
				this.setState({ suggestions: resp }, () => {
				});
			}, error => {
				
			});
		}
		this.setState({ toggleTagInput: !this.state.toggleTagInput });
	}
	
	render() {
		const { postAccept, visibility, tags, toggleTagInput,displaySelectedItem, twitter } = this.state;

		return (
			<div className="empty-inner-container-with-border create-post" >
				<div className="container">
					<div className="row">
						<ul className="list-group w-100 m-0">

							<li className="list-group-item d-flex justify-content-between align-items-center">
								<div className="clearfix content-heading w-100">
									<h3 className="title">Make a post</h3>
									{/* <span className="overlay pull-left"><img className=""
										src={require("../../assets/images/create-plus-icon.png")} /></span> */}
									<textarea
										className="form-control pull-left border-0 p-1"
										placeholder="Create"
										rows="3"
										name="text"
										value={this.state.text}
										onChange={this.handleChange}
									></textarea>
									{/* <ContentEditable
									// onKeyDown={this.handleKeyPress}
									placeholder="What's on your mind?"
									name="text"
									className="form-control pull-left border-0 p-1 textarea"
									innerRef={this.contentEditable}
									html={this.state.text} // innerHTML of the editable div
									disabled={false}       // use true to disable editing
									onChange={this.handleChange} // handle innerHTML change
									tagName='div' // Use a custom HTML tag (uses a div by default)
									/> */}
			
								</div>
							</li>
							{this.state.url != '' &&
                                      <li className="mt-2  d-flex align-items-start justify-content-center">  <i onClick={this.removeURL} className="fa fa-times removeIcon"></i>
									{/* { this.state.url != "" && <ReactTinyLink
  cardSize="small"
  showGraphic={true}
  maxLine={2}
  minLine={1}
  url={this.state.url}
/>} */}
</li>
                                }
							{(displaySelectedItem && displaySelectedItem.length > 0 ) &&
								<li className="list-group-item d-flex justify-content-between align-items-center">
									<div className="clearfix content-heading w-100 pl-2">
										<span className="close-btn" onClick={this.removeSelected}>X</span>
										{displaySelectedItem[0]}
									</div>
								</li>
							}
							{(tags.length > 0) &&
								<li className="list-group-item d-flex justify-content-between align-items-center">
									<div className="clearfix content-heading w-100 tagedPeople">
										<ul>
											{tags.map((value, index) => {
												return <li onClick={() => this.onDelete(index)} className="btn-light" key={index}>{value.name} <span>X</span> </li>
											})}
										</ul>
									</div>
								</li>
							}

							<li className="list-group-item d-flex justify-content-between align-items-center create-post-attachments p-1">
								
								<div className="create-post-customize">
								<div className='hide'>
											<input type='file' name="file" accept={postAccept} onChange={this.handleChangeImage} ref={this.postFileInput} />
										</div>
									<ul className="list-group list-group-horizontal remove-border m-0">
									<li className={"list-group-item p-2"}>
											<button type="button" className="btn btn-light btn-sm tag" onClick={(e) => this.enableUpload(image)}>
												<img className='m-0' src={require("../../assets/images/icons/photo.svg")} />
                      						</button>
										</li>
										
									<li className={"list-group-item p-2"+(!toggleTagInput && ' mr-auto')}>
											<button type="button" className="btn btn-light btn-sm tag" onClick={() => this.toggleTag()}>
												Tag Connection
                      						</button>
										</li>
										{toggleTagInput &&
											<li className="mr-auto list-group-item p-2">
												<ReactTags
													ref={this.reactTags}
													placeholderText="Add Tags"
													suggestions={this.state.suggestions}
													autoresize={false}
													onAddition={this.onAddition.bind(this)} />
											</li>
										}
										{
											this.state.twitterID != null && this.state.twitterID > 0 && <li className="list-group-item p-2 pt-3">
											<img className="pointer smallIcon" onClick={this.changeTwitter} src={require("../../assets/images/" + (twitter ? "twitter-filled.svg" : "twitter-outline.svg"))} />
											<span className="m-1"></span>
										</li>
										}
										
										
										<li className="list-group-item p-2 pt-3">
											<img className="pointer smallIcon" onClick={this.changeVisibility} src={require("../../assets/images/" + (visibility == 'public' ? "create-visibility-icon" : "hide-visibility-icon@2x") + ".png")} />
											<span className="m-1">Visibility</span>
										</li>

										<li className="list-group-item p-2 ">
										
											<button type="button" className="btn btn-sm theme-btn" onClick={this.post}>
												Post
                      						</button>
										</li>
									</ul>
								</div>
								
							</li>
						</ul>
						{
							this.state.loadTxt != '' && <div className="d-block w-100"><small className="text-center d-block text-danger">{this.state.loadTxt}</small><Loader width="100%" color="#5931ea"  /></div>
						}
					</div>
				</div>
			</div >
		);
	}
}

export default CreatePost;
